/* src/POPOSList.css */ 

.POPOSList {

    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    grid-gap: 2em;
    max-width: 1200px;
    margin: auto;
    padding: 2em;
  }

/* Smaller screens and tablets */ 
@media only screen and (max-width: 960px) {
  .POPOSList {
      grid-template-columns: 1fr;
  }
}



